@import url(https://fonts.googleapis.com/css?family=Dosis);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
 body {
	 font-size: 16px;
}
 .calendar-rectangle {
	 width: 100%;
	 position: relative;
	 margin-left: auto;
	 margin-right: auto;
	 color: #616161;
	 font-size: 1em;
	 font-family: 'Dosis', sans-serif;
	 overflow: hidden;
	 box-shadow: 0px 0px 50px #888;
}
 @media (min-width: 576px) {
	 .calendar-rectangle {
		 width: 70%;
	}
}
 @media (min-width: 768px) {
	 .calendar-rectangle {
		 width: 50%;
		 font-size: 1em;
	}
}
 @media (min-width: 992px) {
	 .calendar-rectangle {
		 width: 60%;
		 font-size: 1em;
	}
}
 @media (min-width: 1200px) {
	 .calendar-rectangle {
		 width: 80%;
		 font-size: 1em;
	}
}
 @media (min-width: 1300px) {
	 .calendar-rectangle {
		 width: 80%;
	}
}
 .calendar-rectangle:before {
	 content: "";
	 display: block;
	 padding-top: 120%;
}
 .calendar-content {
	 position: absolute;
	 top: 0;
	 left: 0;
	 bottom: 0;
	 right: 0;
}
 .main-calendar {
	 height: 100%;
	 display: flex;
	 flex-wrap: wrap;
}
 .main-calendar .calendar-header {
	 display: flex;
	 flex-wrap: wrap;
	 width: 100%;
	 height: 30%;
	 color: #fff;
}
 .main-calendar .calendar-header .title-header {
	 width: 100%;
	 height: 70%;
	 white-space: nowrap;
	 font-size: 1.2em;
	 background-color: #1e88e5;
}
 @media (min-width: 992px) {
	 .main-calendar .calendar-header .title-header {
		 font-size: 1.4em;
	}
}
 @media (min-width: 1200px) {
	 .main-calendar .calendar-header .title-header {
		 font-size: 1.2em;
	}
}
 .main-calendar .calendar-header .title-header .header-text {
	 flex: 5 1;
	 display: flex;
	 height: 100%;
}
 .main-calendar .calendar-header .title-header .header-text .today-label {
	 flex: 1 1;
	 font-size: 0.8em;
}
 .main-calendar .calendar-header .title-header .header-text .today-label:hover {
	 cursor: pointer;
	 color: #1e88e5;
	 background-color: #fff;
}
 .main-calendar .calendar-header .title-header .header-text .month-label {
	 flex: 3 1;
}
 .main-calendar .calendar-header .days-header {
	 width: 100%;
	 height: 30%;
	 background-color: #2196f3;
}
 .main-calendar .calendar-header .button-container {
	 width: 100%;
	 height: 30%;
	 background-color: #2196f3;
}
 .main-calendar .calendar-header .button-container .event-button {
	 flex-grow: 1;
	 display: flex;
	 height: 100%;
	 align-items: center;
	 justify-content: center;
}
 .main-calendar .calendar-header .button-container .event-button:hover {
	 background-color: #fff;
	 color: #2196f3;
}
 .main-calendar .days-container {
	 width: 100%;
	 height: 70%;
	 background: #fff;
}
 .main-calendar .days-container .week {
	 height: 15%;
}
 .main-calendar .day-events {
	 position: relative;
	 width: 100%;
	 height: 70%;
	 background-color: #fff;
	 font-size: 1.2em;
}
 .main-calendar .day-events .event-container {
	 width: 100%;
	 text-align: center;
	 display: flex;
}
 .main-calendar .day-events .event-container:hover {
	 cursor: pointer;
}
 .main-calendar .day-events .event-container .animated-time {
	 width: 30%;
}
 .main-calendar .day-events .event-container .animated-title {
	 width: 70%;
}
 .main-calendar .day-events .event-container .event-attribute {
	 height: 100%;
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 overflow: hidden;
	 box-sizing: border-box;
	 padding: 5px;
}
 .row-c {
	 display: flex;
	 justify-content: center;
	 align-items: center;
}
 .box {
	 flex-grow: 1;
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 flex: 1 1;
	 height: 100%;
	 transition: all 0.4s ease-in-out 0s;
}
 .box.arrow:hover {
	 background-color: white;
	 cursor: pointer;
	 color: #1e88e5;
	 transition: all 0.2s ease-in-out 0s;
}
 .day {
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 flex: 1 1;
	 height: 100%;
}
 .day .day-number {
	 width: 80%;
	 height: 90%;
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 border: 1px solid #fff;
	 box-sizing: border-box;
	 border-radius: 50%;
}
 .day:hover .day-number {
	 cursor: default;
	 background-color: #90caf9;
	 color: #fff;
	 transition: background-color 0.2s ease-in-out 0s;
}
 .day.today .day-number {
	 border: 1px solid #90caf9;
}
 .day.has-events .day-number {
	 color: #1e88e5;
	 font-weight: bold;
}
 .day.different-month {
	 opacity: 0.5;
}
/* time animation */
 .time-appear {
	 transform: translateX(-100%);
	 transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}
 .time-appear.time-appear-active {
	 transform: translateX(0);
	 transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}
 .time-enter {
	 transform: translateX(-100%);
	 transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}
 .time-enter.time-enter-active {
	 transform: translateX(0);
	 transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}
 .time-leave {
	 transform: translateX(-100%);
	 transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}
 .time-leave.time-leave-active {
	 transform: translateX(0);
	 transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}
/* title animation */
 .title-appear {
	 transform: translateX(100%);
	 transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}
 .title-appear.title-appear-active {
	 transform: translateX(0);
	 transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}
 .title-enter {
	 transform: translateX(100%);
	 transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}
 .title-enter.title-enter-active {
	 transform: translateX(0);
	 transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}
 .title-leave {
	 transform: translateX(100%);
	 transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}
 .title-leave.title-leave-active {
	 transform: translateX(0);
	 transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}
 
